@import "../../common/styles/sass/variables";
@import "../../common/styles/sass/mixins";


.socialsWrapper {
  width: 100%;
  @include flexPositioning(space-between, stretch);


  .socialsBar {
    width: 200px;
    @include flexPositioning(flex-start, center);
  }


  .navBar {
    width: 600px;
    @include flexPositioning(flex-end, stretch);
    position: relative;

    .burgIcon {
      display: none;
    }

    .nav {
      width: 100%;
      @include flexPositioning(space-between, center);
      flex-direction: row;
      margin: 40px 40px;

      a {
        @include text(20px, 800);
        text-decoration: none;
        color: $primaryColor;
        cursor: pointer;

        &:hover {
          color: $accentColor;
        }
      }

      .active {
        color: $accentColor;
      }

      .closeIcon {
        display: none;
      }
    }

    @media screen and (max-width: 688px) {
      height: 10vh;
      width: 70%;
      position: relative;
      @include flexPositioning(flex-end, center);

      .burgIcon {
        display: block;
        height: 50px;
        margin: 10px;
        width: 50px;
        cursor: pointer;
      }

      .hideBtn {
        display: none;
      }

      .nav {
        display: none;

        .closeIcon {
          display: block;
          position: absolute;
          top: 0;
          right: 0;
          margin: 10px 15px;
          cursor: pointer;
        }
      }

      .showMenu {
        background-color: $secondaryColor;
        position: absolute;
        border-radius: $borderRadius;
        margin: 20px;
        height: 40vh;
        width: 250px;
        @include flexPositioning(space-evenly, center);
        //background-image: url("https://mir-s3-cdn-cf.behance.net/project_modules/fs/882ca886128681.5d909dad4e52d.jpg");
        box-shadow: 0 5px 38px 35px rgba(24, 41, 54, 0.2);
        background-position: bottom;
        top: 0;
        right: 0;
        flex-direction: column;
        gap: 10px;
        padding: 10px 30px;
        z-index: 1;
      }
    }
  }
}
